import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import 'rxjs/add/operator/map';
import {JwtAuthenticationRequest} from '../models/JwtAuthenticationRequest';
import {Router} from '@angular/router';
import {Subject} from 'rxjs/Subject';
import * as jwt_decode from 'jwt-decode';

@Injectable()
export class AuthenticationService {

    public loggedIn = new Subject;

    constructor(private http: HttpClient, private router: Router) {
    }

    isLoggedIn(): boolean {
        if (localStorage.getItem('token')) {
            return true;
        } else {
            return false;
        }
    }

    getRole(): string {
        return localStorage.getItem('role').replace(/['"]+/g, '');
    }

    getUsername(): string {
        return localStorage.getItem('username');
    }

    login(jwtAuthenticationRequest: JwtAuthenticationRequest) {
        return this.http.post<any>('http://localhost:8080/auth', jwtAuthenticationRequest)
            .map(token => {
                const decoded = jwt_decode(token.token);
                if (token && decoded) {
                    // store user details and jwt token in local storage to keep user logged in between page refreshes
                    localStorage.setItem('token', JSON.stringify(token.token));
                    const role = decoded.roleName[0];
                    localStorage.setItem('role', JSON.stringify(role['authority'].substring(5)));
                    localStorage.setItem('username', decoded.username);
                }
                this.loggedIn.next(true);
                this.router.navigate(['/dashboard']);
                return token;
            });
    }

    logout() {
        // remove user from local storage to log user out
        this.loggedIn.next(false);
        localStorage.removeItem('token');
        localStorage.removeItem('role');
        localStorage.removeItem('username');
        this.router.navigate(['/login']);
    }
}



