import {Injectable} from '@angular/core';
declare var $: any;

@Injectable()
export class NotificationService {

    constructor() {
    }

    showNotification(from, align, type, message) {
        $.notify({
            icon: 'notifications',
            message: message

        }, {
            type: type,
            timer: 2000,
            placement: {
                from: from,
                align: align
            }
        });
    }
}



